import React, { useState } from 'react';
import { Table } from 'react-bootstrap';
import { connect } from 'react-redux';
import './Detail.scss';

function Cart(props) {
	let [render, setRender] = useState(true);
	return (
		<div>
			<Table responsive>
				<thead>
					<tr>
						<th>상품명</th>
						<th>수량</th>
						<th>가격</th>
						<th>변경</th>
					</tr>
				</thead>
				<tbody>
					<LocalStorageCart render={render} setRender={setRender}></LocalStorageCart>
					{/* {props.state.map((a, i) => {
						return (
							<tr key={i}>
								<td>{a.name}</td>
								<td>{a.quan}</td>
								<td>{a.price}</td>
								<td>
									<button
										onClick={() => {
											props.dispatch({
												type: 'quanUp',
												data: a.quan,
											});
											props.setRender(!props.render);
										}}
									>
										+
									</button>
									<button
										onClick={() => {
											props.dispatch({
												type: 'quanDown',
												data: i,
											});
										}}
									>
										-
									</button>
									<button
										onClick={() => {
											props.dispatch({
												type: 'delete',
												data: a.name,
											});
										}}
									>
										삭제
									</button>
								</td>
							</tr>
						);
					})} */}
				</tbody>
			</Table>
			<TotalPrice state={props.state}></TotalPrice>
			{props.alert === true ? (
				<div className='my-alert'>
					<p>지금 구매하시면 신규할인 20%</p>
					<button
						className='cart-alert-close'
						onClick={() => {
							props.dispatch({ type: 'alertClosed' });
						}}
					>
						X
					</button>
				</div>
			) : null}
		</div>
	);
}

function LocalStorageCart(props) {
	let arr = localStorage.getItem('cart');
	if (arr == null) {
		arr = [];
	} else {
		arr = JSON.parse(arr);
	}
	return(
		arr.map((a) => {
			let found = arr.findIndex((a) => {
				return a.name === arr.name;
			});
			return (
				<tr>
					<td>{a.name}</td>
					<td>{a.quan}</td>
					<td>{a.price}</td>
					<td>
						<button
							onClick={() => {
								props.setRender(!props.render);
								a.quan++;
								localStorage.setItem('cart', JSON.stringify(arr));
							}}
						>
							+
						</button>
						<button
							onClick={() => {
								props.setRender(!props.render);
								if (a.quan > 1) {
									a.quan--;
								}
								localStorage.setItem('cart', JSON.stringify(arr));
							}}
						>
							-
						</button>
						<button
							onClick={() => {
								props.setRender(!props.render);
								arr.splice(found, 1);
								localStorage.setItem('cart', JSON.stringify(arr));
							}}
						>
							삭제
						</button>
					</td>
				</tr>
			);
		})
	)
}

function TotalPrice(props) {
	let arr = localStorage.getItem('cart');
	arr = JSON.parse(arr);
	let price = 0;
	arr.map((a) => {
		price = price + a.price * a.quan;
	});
	return (
		<div>
			<div>총 상품가격 : {price}</div>
			<div>총 배송비 : 0</div>
			<div>총 결제 예상 금액 : {price - (price * 20) / 100}</div>
		</div>
	);
}

function state를props화(state) {
	return {
		state: state.reducer,
		alert: state.reducer2,
	};
}

export default connect(state를props화)(Cart);
