let alert_reducer = true;

function reducer2(state = alert_reducer, action) {
	if (action.type === 'alertClosed') {
		state = false;
		return state;
	} else {
		return state;
	}
}

let product_reducer = [];

function reducer(state = product_reducer, action) {
	if (action.type === 'product_add') {
		let found = state.findIndex((a) => {
			return a.id === action.data.id;
		});

		if (found >= 0) {
			let copy = [...state];
			copy[found].quan += action.data.quan;
			return copy;
		} else {
			let copy = [...state];
			copy.push(action.data);
			return copy;
		}
	} else if (action.type === 'quanUp') {
		let copy = [...state];
		copy[action.data].quan++;
		return copy;
	} else if (action.type === 'quanDown') {
		let copy = [...state];
		if (copy[action.data].quan <= 1) {
			return copy;
		} else {
			copy[action.data].quan--;
			return copy;
		}
	} else if (action.type === 'delete') {
		let found = state.findIndex((a) => {
			return a.name === action.data;
		});
		let copy = [...state];
		copy.splice(found, 1);
		return copy;
	} else {
		return state;
	}
}

export { reducer, reducer2 };
